@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

* {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    margin: auto;
    background-color: #ffffff;
    font-family: 'Poppins', sans-serif;
}

.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}

/*--------------------------------------------------------------
# General
--------------------------------------------------------------*/
ul {
    list-style: none;
}

a {
    text-decoration: none;
}

button {
    outline: none;
    border: none;
}

input {
    outline: none;
    border: none;
}

/*--------------------------------------------------------------
# Nav Section
--------------------------------------------------------------*/
.logo img {
    max-height: 40px;
    max-width: 100%;
    height: auto;
}

nav {
    display: flex !important;
    align-items: center !important;
    position: fixed;
    width: 100%;
    height: 70px;
    left: 0;
    top: 0;
    background-color: #fff;
    border-bottom: 1px solid #e1e1e1;
    transition: 0.6s ease-in-out;
    z-index: 99;
}

.bliz-nav {
    display: flex !important;
    justify-content: space-between !important;
    align-items: center !important;
    width: 100%;
    max-width: 1500px;
    margin: 0 auto;
    padding-right: 60px;
    padding-left: 60px;
}

nav.active {
    box-shadow: 5px 10px 30px rgba(255, 118, 29, 0.157);
    background-color: #ffffff;
    opacity: 0.9;
}

nav ul {
    display: flex;
}

.active {
    background-color: #ff761d;
    color: #ffffff;
    box-shadow: 5px 10px 30px rgba(255, 118, 29, 0.411);
    transition: all ease 0.2s;
}

nav ul li a {
    font-family: 'Poppins';
    color: #000000;
    height: 40px;
    line-height: 43px;
    margin: 3px;
    padding: 0px 22px;
    display: flex;
    font-size: 0.9rem;
    text-transform: uppercase;
    font-weight: 500;
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.2s ease-in-out;
}


nav li.login a:hover {
    background-color: #f9f9f9;
    color: #FF761D;
    transition: all ease 0.2s;
}

nav li.register a {
    background-color: #FF761D;
    color: #ffffff;
    box-shadow: 5px 10px 30px rgba(233, 132, 0, 0.336);
}

nav li.register a:hover {
    background-color: #ffffff;
    color: #FF761D;
    box-shadow: 5px 10px 30px rgba(233, 132, 0, 0.336);
    transition: all ease 0.2s;
}

nav .menu-btn,
.menu-icon {
    display: none;
}


/*--------------------------------------------------------------
# Main Section
--------------------------------------------------------------*/
#main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 690px;
    position: relative;
    padding: 0 120px;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url('./img/background.svg');
}

.left-main {
    font-family: 'Poppins';
    max-width: 60%;
    width: 500px;
}

.right-main {
    display: flex;
    max-width: 50%;
    font-family: 'Poppins';
}

.left-main .details {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    color: #787878;
}

.left-main h1 {
    font-family: 'Poppins';
    font-size: 40px;
    font-weight: 700;
    line-height: 98.5%;
    margin: 0px;
    letter-spacing: 2px;
    color: #FF761D;
}

/*--------------------------------------------------------------
# Features Section
--------------------------------------------------------------*/
#features {
    display: flex;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    font-family: 'Poppins';
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px;
    background-color: #F7FAFD;
}

.f-container {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
}

.f-box {
    background-color: #ffffff;
    width: 300px;
    padding: 20px;
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 10px 12px rgba(0, 0, 0, 0.1);
    transition: 0.3s ease-in-out;
    position: relative;
}

.f-box h2 {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 157%;
}

.f-box p {
    font-weight: 400;
    font-size: 14px;
    color: #787878;
}

.f-box:hover {
    box-shadow: 2px 2px 12px rgba(0, 0, 0, 0.1);
}

/*--------------------------------------------------------------
# Privacy Section
--------------------------------------------------------------*/
#privacy {
    width: 100%;
    height: auto;
    padding: 50px 5% 0px 5%;
    position: relative;
}

.privacy-title h2,
h3 {
    font-family: 'Poppins';
    line-height: 1.2;
    font-weight: 700;
    text-align: center;
}

.privacy-title h3 {
    font-weight: 600;
}

.privacy-container {
    display: flex;
    justify-content: space-between;
    padding: 0 50px;
    align-items: center;
}

.privacy-text {
    width: 60%;
    font-size: 1.1rem;
    color: #000000;
}

/* .privacy-img {
    width: 30%;
    max-width: 40%;
} */

/*--------------------------------------------------------------
# Presentation Section
--------------------------------------------------------------*/
#presentation {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 120px;
    padding-top: 30px;
    width: 100%;
    height: 585px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('./img/presentation.svg');
}

.content {
    text-align: start;
}

.content h3 {
    text-align: start;
}

footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #FF761D;
    color: #ffffff;
    padding: 30px 120px;
}

.bliz-footer {
    display: flex !important;
    justify-content: space-between;
    align-items: center !important;
    width: 100%;
    max-width: 1500px;
    margin: 0 auto;
    padding-right: 60px;
    padding-left: 60px;
}

.social-media a {
    font-size: 20px;
    color: #fff;
    margin: 10px;
}

.social-media a .fa-twitter:hover {
    color: #1c93e4;
    transition: 0.5s;
}

.social-media a .fa-facebook:hover {
    color: #1771e6;
    transition: 0.5s;
}

.social-media a .fa-instagram:hover {
    color: #9e3698;
    transition: 0.5s;
}

.social-media a .fa-youtube:hover {
    color: #f10002;
    transition: 0.5s;
}

@media(min-width:1400px) {
    .container {
        max-width: 1180px;
        padding-right: 20px;
        padding-left: 20px;
    }
}

@media(max-width:1200px) {
    #main {
        height: 490px;
    }


    .right-main {
        max-width: 400px;
    }

    .right-main .img-hero {
        height: auto;
        max-width: 100%;
    }

    .f-box{
        flex-grow: 1;
    }

    .privacy-text{
        width: 70%;
    }
    .privacy-img{
        width: 30%;
    }
    .privacy-img img{
        max-width: 250px;
    }
    #presentation{
        padding-right: 50px;
    }
}

@media only screen and (max-width:768px) {

    #main {
        justify-content: space-around;
        height: 690px;
        padding: 0 20px;
        flex-wrap: wrap;
        width: 100%;
    }

    .right-main {
        flex-wrap: 0 0 100%;
        order: 1;
    }

    .right-main .img-hero {
        max-width: 300px;
        vertical-align: middle;
    }

    .left-main {
        max-width: 100%;
        text-align: center;
        order: 2;
    }

    .privacy-container {
        justify-content: space-around;
        padding: 0 10px;
    }

    .privacy-text {
        width: 80%;
        text-align: center;
    }

    .privacy-img {
        display: none;
    }

    #presentation{
        padding-top: 20px;
        padding-right: 2px;
        padding-left: 2px;
        align-items: center;
        justify-content: center;
    }

    .content {
        padding: 0 10px;
        background: rgba(255, 255, 255, 0.5);
        border-radius: 10px;
    }

    footer{
        padding: 20px 10px;
        text-align: center;
    }

    .bliz-footer{
        justify-content: space-around;
        flex-wrap: wrap;
        padding: 0px;
    }

/*--------------------------------------------------------------
# Menu Icon
--------------------------------------------------------------*/
    .menu-icon {
        display: block;
    }

    nav {
        justify-content: space-between;
        height: 65px;
    }

    .bliz-nav {
        padding-right: 20px;
        padding-left: 20px;
    }

    .logo img {
        max-height: 26px;
        max-width: 100%;
        height: auto;
    }

    .header-btns {
        margin: 0;
    }

    .menu {
        display: none;
        position: absolute;
        top: 65px;
        left: 0px;
        background-color: #FFFFFF;
        border-bottom: 4px solid #333;
        width: 100%;
        padding: 0px;
        margin: 0px;
    }

    .menu li {
        width: 100%;
    }

    nav .menu li a {
        width: 100%;
        height: 40px;
        justify-content: center;
        align-items: center;
        margin: 0px;
        padding: 25px;
        border: 1px solid rgba(38, 38, 38, 0.03);
    }

    nav .menu-icon {
        cursor: pointer;
        float: right;
        padding: 28px 20px;
        position: relative;
        user-select: none;
    }

    nav .menu-icon .nav-icon {
        background-color: #333333;
        display: block;
        height: 2px;
        position: relative;
        transition: background 0.2s ease-out;
        width: 18px;
    }

    nav .menu-icon .nav-icon:before,
    nav .menu-icon .nav-icon:after {
        background: #333;
        content: '';
        display: block;
        height: 100%;
        position: absolute;
        transition: all ease-out 0.2s;
        width: 100%;
    }

    nav .menu-icon .nav-icon:before {
        top: 5px;
    }

    nav .menu-icon .nav-icon:after {
        top: -5px;
    }

    nav .menu-btn:checked~.menu-icon .nav-icon {
        background: transparent;
    }

    nav .menu-btn:checked~.menu-icon .nav-icon:before {
        transform: rotate(-45deg);
        top: 0;
    }

    nav .menu-btn:checked~.menu-icon .nav-icon:after {
        transform: rotate(45deg);
        top: 0;
    }

    nav .menu-btn {
        display: none;
    }

    nav .menu-btn:checked~.menu {
        display: block;
    }

    #presentation {
        background-size: cover;
    }
}

@media only screen and (max-width: 530px) {
    #main {
        padding: 20px;
    }
    
}
